import React, { useState } from 'react'
import Form from '../../../../components/Form'
import "./sign-up.css"
import { NavLink, useNavigate } from 'react-router-dom'
import { bottomSvg, leftSvg, rightSvg, brandLogo, otpImage, useWindowWidth } from '../..'
import {useDispatch} from"react-redux"
import { signupAction } from '../../actions/authAction'

const SignUpForm = () => {
    const {screenWidth} = useWindowWidth()
    const [userFormData, setUserFormData] = useState({
        username: "",
        email: "",
        password: "",
        confirm_password: "",
        gender: "",
        isAbove18: "yes",
    })

    const dispatch = useDispatch()
    const navigate = useNavigate()

    const template = {
        title: screenWidth < 618 && "Sign up",
        confirmedBtnText: "Sign Up",
        fields: [
            {
                name: "username",
                type: "text",
                placeholder: "User name",
                value: userFormData.username
            },
            {
                name: "email",
                type: "email",
                placeholder: "Email address",
                value: userFormData.email
            },
            {
                name: "password",
                type: "password",
                placeholder: "Password",
                value: userFormData.password
            },
            {
                name: "confirm_password",
                type: "password",
                placeholder: "Confirm Password",
                value: userFormData.confirm_password
            },
            {
                name: "gender",
                type: "text",
                placeholder: "Gender",
                value: userFormData.gender
            },
            // {
            //     name: "isAbove18",
            //     type: "date",
            //     placeholder: "MM/DD/YYYY",
            //     value: userFormData.isAbove18
            // }
        ]
    }

    const handleFormChange = (e) => {
        try{
            const {name, value} = e.target;
            setUserFormData(oldData => {
                return {
                    ...oldData,
                    [name]: value
                }
            })

        }
        catch(error){
            console.log(error)
        }
    }

    const handleFormSubmit = async (e) => {
        e.preventDefault()
        try {
            if(userFormData && userFormData.password !== userFormData.confirm_password){
                throw new Error("Password does not match")
            }
            const options = {
                method: "POST",
                body: JSON.stringify(userFormData),
            };

            dispatch(signupAction(options))  
            
            setUserFormData({
                username: "",
                email: "",
                password: "",
                confirm_password: "",
                gender: "",
                isAbove18: "yes",
            })
            navigate("/otp")
          } catch (error) {
            console.error("Signup error:", error.message);
        }
    }

  return (
    <article className="sign-up-container">

        <div className="register-wrapper">

            {
                screenWidth < 618 && <div className="register-bg-icons-container">
                    <img src={rightSvg} className="register-bg-icon" alt="backgroud-icon" />
                    <img src={leftSvg}  className="register-bg-icon" alt="backgroud-icon" />
                    <img src={bottomSvg}  className="register-bg-icon" alt="backgroud-icon" />
                </div>
            }

            {
                screenWidth >= 618 && <div className="register-image-container">
                    <img src={otpImage} className="register-image" alt="register" />
                    {screenWidth >= 618 && <h3>Welcome back</h3>}
                    {screenWidth >= 618 && <p>...bridging the gap between social media and mental health.</p>}
                </div>
            }

            <div className="register-content-container">
                <div className="register-brand-container">
                    <img src={brandLogo} className="register-brand-logo" alt="brand-logo" />
                </div>

                <Form template={template} className={"register-form"} onSubmit={handleFormSubmit} onChange={handleFormChange}/>

                <div className="register-disclaimer">
                    <p className="disclaimer-text">
                        By Signing up, you agree to the <NavLink>Terms of use</NavLink> and <NavLink>Privacy Policy</NavLink>
                    </p>

                    <p className="disclaimer-text">
                        Already have an account? <NavLink>Sign in</NavLink>
                    </p>
                </div>
            </div>
      </div>
    </article>
  )
}

export default SignUpForm
