import React from 'react'

const DynamicFormField = ({
    label, 
    name, 
    onChange, 
    value, 
    type,
    className,
    required,
    placeholder
}) => {
    const renderFormField = () => {
        switch(type){
            case "text":
                return (
                    <input 
                        type={type} 
                        name={name} 
                        value={value}
                        id={label}
                        className={className}
                        required={required}
                        placeholder={placeholder} 
                        onChange={onChange} 
                    />
                )
                break;
            case "date":
                return(
                    <input 
                        type={type} 
                        name={name} 
                        value={value}
                        id={label}
                        className={className}
                        required={required}
                        placeholder={placeholder} 
                        onChange={onChange} 
                    />
                )
                break;
            case "checkbox":
                return (
                    <input
                        type={"checkbox"} 
                        name={name} 
                        value={value}
                        id={label}
                        className={className}
                        required={required}
                        onChange={onChange}
                    />
                )
                break;
            case "textarea":
                return (
                    <textarea
                        name={name}
                        value={value}
                        id={label}
                        className={className}
                        required={required}
                        placeholder={placeholder} 
                        onChange={onChange} 
                    />
                )
                break;
            case "number":
                return (
                    <input
                        type="number"
                        name={name}
                        value={value}
                        id={label}
                        placeholder={placeholder}
                        onChange={onChange}
                        maxLength={1}
                        required={required}
                    />
                )
                break;
            case "email":
                return (
                    <input
                        type="email"
                        name={name}
                        value={value}
                        id={label}
                        placeholder={placeholder}
                        onChange={onChange}
                        required={required}
                    />
                )
                break;
            case "password":
                return (
                    <input
                        type="password"
                        name={name}
                        value={value}
                        id={label}
                        placeholder={placeholder}
                        onChange={onChange}
                        required={required}
                        autoComplete="new-password"
                    />
                )
                break;
            case "tel":
                return (
                    <input
                        type="tel"
                        name={name}
                        value={value}
                        id={label}
                        placeholder={placeholder}
                        onChange={onChange}
                        required={required}
                        maxLength={1}
                    />
                )
                break;
            default:
                return (
                    <input 
                        type="text" 
                        name={name} 
                        value={value} 
                        id={label}
                        placeholder={placeholder} 
                        onChange={onChange} 
                        required={required} 
                    />
                )

        }
    }

  return (
    <div>
        {label && <label htmlFor={label}>{label}</label>}
        {renderFormField()}
    </div>
  )
}

export default DynamicFormField
