import { createAsyncThunk } from "@reduxjs/toolkit";
import { API_URL } from "../../../api";
import { getFetch } from "../../../../libs/fetch";

export const deletePost = createAsyncThunk(
  "posts/deletePost",
  async ({ options, _id }) => {
    const data = await getFetch(`${API_URL}/post/deletePost/${_id}`, options);
    console.log(data);
    return data;
  }
);
