import React from 'react'
import "./societies-list.css"
import societyImage from "../../assets/images/society.png"

const SocietiesList = () => {
    const societies = [
        {
          name: "Addiction",
          member: "34k member"
        },
        {
          name: "Anxiety",
          member: "34k member"
        },
        {
          name: "Relationship",
          member: "34k member"
        },
        {
          name: "Trauma",
          member: "34k member"
        },
        {
          name: "Parenting",
          member: "34k member"
        },
        {
          name: "Postpartun Depression",
          member: "34k member"
        },
        {
          name: "Marital",
          member: "34k member"
        },
        {
          name: "Loneliness",
          member: "34k member"
        },
        {
          name: "Academics",
          member: "34k member"
        },
        {
          name: "Abuse",
          member: "34k member"
        },
        {
          name: "Impostor Syndrome",
          member: "34k member"
        },
        {
          name: "Depression",
          member: "34k member"
        },
        {
          name: "Finance",
          member: "34k member"
        },
        {
          name: "Insecurity",
          member: "34k member"
        },
      ]

  return (
    <section className="society-container">
        <article className="societies-list-wrapper">
            <h2>Societies I joined</h2>

            <ul className="societies-list">
                {societies.map(society => {
                    return<li className="societies-list-item">
                        <div className="societies-list-image">
                            <img src={societyImage} alt="society" />
                        </div>
                        <div className="societies-list-text">
                            <h3>{society.name}</h3>
                            <small>{society.member}</small>
                        </div>
                    </li>
                })}
            </ul>
        </article>
      
    </section>
  )
}

export default SocietiesList
