import React, { useState } from 'react'
import Form from '../../../../components/Form'
import "./login.css"
import { NavLink } from 'react-router-dom'
import brandLogo from '../../assets/images/ventmoir.png'
import { bottomSvg, leftSvg, otpImage, rightSvg, useWindowWidth } from '../..'
import { useDispatch } from 'react-redux'
import { loginAction } from '../../actions/authAction'

const LoginForm = () => {
  const {screenWidth} = useWindowWidth()
  const [loginCredentials, setLoginCredentials]= useState({
    username: "",
    password: "",
    anonymous: ""

  })
  const dispatch = useDispatch()


  const template = {
    title: screenWidth < 618 && "Login",
    confirmedBtnText: "Sign in",
    fields :[
      {
        name: "username",
        value: loginCredentials.username,
        type: "text",
        placeholder: "Username"
      },
      {
        name: "password",
        value: loginCredentials.password,
        type: "password",
        placeholder: "Password"
      },
    ]
  }

  const handleFormChange = (e) => {
    try{
      const { name, value} = e.target;
      setLoginCredentials(oldData => {
        return {
          ...oldData,
          [name]: value
        }
      })

    }
    catch(error){
      // console.log(error)
    }
  }

  const handleUserLogin = (e) => {
    e.preventDefault()
    try{
      const options = {
        method: "POST",
        body: JSON.stringify(loginCredentials),
    };

      dispatch(loginAction(options))
      console.log(loginCredentials)
      // setLoginCredentials({
      //   username: "",
      //   password: "",
      //   anonymous: ""
      // })
    }
    catch(error){
      // console.log(error)
    }
  }

  return (
    <article className="login-container">
        <div className="login-wrapper">

         {
          screenWidth < 618 && <div className="login-bg-icons-container">
              <img src={rightSvg} className="login-bg-icon" alt="backgroud-icon" />
              <img src={leftSvg}  className="login-bg-icon" alt="backgroud-icon" />
              <img src={bottomSvg}  className="login-bg-icon" alt="backgroud-icon" />
          </div>
          }

          {
            screenWidth >= 618 && <div className="login-image-container">
              <img src={otpImage} className="login-image" alt="login" />
              {screenWidth >= 618 && <h3>Welcome back</h3>}
              {screenWidth >= 618 && <p>...bridging the gap between social media and mental health.</p>}
            </div>
          }

          <div className="login-content-container">
            <div className="login-brand-container">
              <img src={brandLogo} className="login-brand-logo" alt="brand-logo" />
            </div>

            <Form template={template} onSubmit={handleUserLogin} className={"login-form"} onChange={handleFormChange}/>

            <div className="new-user-call-to-action-container">
              <p className="call-to-action-text">
                Not a member? <NavLink to="/register">Sign up</NavLink>
              </p>
            </div>
        </div>
      </div>
    </article>
  )
}

export default LoginForm


