import React, { useState } from 'react'
import "./user-option.css"
import { followUser } from '../../services/user/actions/folllowUser';
import { useDispatch } from 'react-redux';
import { getCurrentUser } from '../../libs/getCurrentUser';
import { deletePost } from '../../services/post/actions/crud/deletePost';

const UserOption = ({ user, options, postId}) => {
  const [selectedOption, setSelectedOption] = useState(null);
  const dispatch = useDispatch()
  const currentUser = getCurrentUser()
  const {_id} = user

  console.log(user)

  const userName = _id === currentUser.userUid && user.userName

  const handleOptionClick = (option) => {
    setSelectedOption(option);
    switch (option.label) {
      case 'Follow @Irawo':
        const options = {
          method: "PATCH",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${currentUser.userToken}
            `,
          },
        };
        dispatch(followUser({options, _id}))
        console.log("follow")
        break;
      case 'Copy Link':
        console.log("copyLink")
        // performFunction2();
        break;
      case 'Mute @Irawo':
        console.log("Mute")
        // performFunction3();
        break;
      case 'Block @Irawo':
        console.log("Block")
        // performFunction3();
        break;
      case 'Report Post':
        // return ("report")
        // performFunction3();
        break;
      case 'Delete Post':
        console.log(postId)
        const payload = {
          options: {
            method: "DELETE",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${currentUser.userToken}`,
            },
          },
          _id: postId
        }
        console.log("delete", payload)
        return dispatch(deletePost(payload))
        break;
      default:
        break;
    }
  };

  const handleUserAction = (option) => {
    if (option && option.action) {
      return option.action()
    }
  };

  return (
    <aside className="user-option-container">
      <ul className="option-list">
        {options.map((option) => (
          <li key={option.label} onClick={() => handleOptionClick(option)}>
            {option.label}
          </li>
        ))}
      </ul>
    </aside>
  )
}

export default UserOption
