import React, { useRef, useState } from 'react'
import useToggle from '../hooks/useToggle'
import Button from '../components/ui/Button'
import useWindowWidth from '../hooks/useWindowWidth'

const Ts = ({length = 6}) => {
    const [v, togg] = useToggle()
    const {screenWidth} = useWindowWidth()

    const register = async () => {
      const url = "https://ventmoir.onrender.com/user/signup"
      const data = await fetch(url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          "username": "irawo",
          "email": "irawo167@gmail.com",
          "password":"irawo167",
          "confirm_password": "irawo167",
          "gender":"Male",
          "isAbove18":"yes"
        })
      })

      const res = await data.json()
      console.log(res)
      console.log("Signing in")
    }

    const [otp, setOtp] = useState(Array(6).fill(""));
    const inputRefs = useRef([...Array(length)].map(() => React.createRef()))

    const onComplete = (otp) => {
      console.log('OTP entered:', otp);
    };

    const handleInputChange = (value, index) => {
      if(isNaN(value)) return;

      const newOtp = [...otp]
      newOtp[index] = value;
      setOtp(newOtp)

      // Move to the next input field if value is not empty
      if (value !== '' && index < length - 1) {
        inputRefs.current[index + 1].focus();
      }

      // Call onComplete callback if all inputs are filled
      if (newOtp.every((entry) => entry !== '')) {
          onComplete(newOtp.join(''));
      }
      
    }

    const handleInputKeyDown = (event, index) => {
      // Move to the previous input field on backspace if current input is empty
      if (event.key === 'Backspace' && index > 0 && otp[index] === '') {
        inputRefs.current[index - 1].focus();
      }
    };  

  return (
    <div>
        <h2>Test</h2>

        {v && "Yup"}
        <Button text={"Show"} handleEvent={togg}/>
        {screenWidth}

      <br />
      <Button text={"Signup"} handleEvent={register}/>

      {otp.map((value, index) => (
        <input
        style={{border: ".1rem solid black", width: "2rem", height: "20px"}}
          key={index}
          type="text"
          maxLength="1"
          value={value}
          onChange={(e) => handleInputChange(e.target.value, index)}
          onKeyDown={(e) => handleInputKeyDown(e, index)}
          ref={(ref) => (inputRefs.current[index] = ref)}
        />
      ))}
      {"yes"}
    </div>
  )
}

export default Ts
